function myReducer (state=initialState, action){
	if (action.type=='moveJet'){
		switch (action.payload) {
			case 'ArrowUp':
				if(parseFloat(state.jetPosition.bottomDistance)<40){
					return {
						...state,
						jetPosition:{
							bottomDistance:parseFloat(state.jetPosition.bottomDistance)+5+'vh',
							leftDistance:state.jetPosition.leftDistance,
						}
					};
				}else{
					return state;
				}
			case 'ArrowDown':
				if(parseFloat(state.jetPosition.bottomDistance)>0){
					return {
						...state,
						jetPosition:{
							bottomDistance:parseFloat(state.jetPosition.bottomDistance)-5+'vh',
							leftDistance:state.jetPosition.leftDistance,
						}
					};
				}else{
					return state;
				}
			case 'ArrowRight':
				if((parseFloat(document.getElementById('jet-plane').style.left))/100*Math.min.apply(null,[window.innerHeight,window.innerWidth])<(window.innerWidth/
					Math.min.apply(null,[window.innerHeight,window.innerWidth]))*(Math.min.apply(null,[window.innerHeight,window.innerWidth]))-
					(window.innerHeight/10)) {
					return {
						...state,
						jetPosition:{
							bottomDistance:state.jetPosition.bottomDistance,
							leftDistance:parseFloat(state.jetPosition.leftDistance)+5+'vh',
						}
					};
				}else {
					return state;
				}				
			case 'ArrowLeft':
				if(parseFloat(state.jetPosition.leftDistance)>0){
					return {
						...state,
						jetPosition:{
							bottomDistance:state.jetPosition.bottomDistance,
							leftDistance:parseFloat(state.jetPosition.leftDistance)-5+'vh',
						}
					};
				}else{
					return state;
				}
			default:
				return state;
				
		} 
	}else if (action.type=='initializeArrow') {
		
		
		return {
			...state,
				arrowPositions: {
					...state.arrowPositions,
					[action.payload]:{
						...state.arrowPositions[action.payload],
							display:'inline',
							initialized:'yes',
							leftDistance:state.jetPosition.leftDistance,
							bottomDistance:state.jetPosition.bottomDistance,
							life:1
					}
				}
									
				
			
				
		}
	 }else if(action.type=='moveArrow'){
		 
		 return {
			...state,
				arrowPositions: {
					...state.arrowPositions,
					[action.payload]:{
						...state.arrowPositions[action.payload],
							bottomDistance:parseFloat(state.arrowPositions[action.payload].bottomDistance)+5+'vh',
							life:state.arrowPositions[action.payload].life+1,
					}
				}
		 }
	 }else if(action.type=='mouseDown'){
		 
		 return {
			 ...state,
				moveProperties: {
					...state.moveProperties,
						touched:'yes',
						pointerY:action.pageY,
						pointerX:action.pageX,
						jetY:parseFloat(state.jetPosition.bottomDistance)*(window.innerHeight/100),
						jetX:parseFloat(state.jetPosition.leftDistance)*(window.innerHeight/100)
				}
		 }
	 }else if (action.type=='mouseYMover' && state.moveProperties.touched=='yes' ) {
		if ((((state.moveProperties.jetY)-(action.pageY-state.moveProperties.pointerY))/window.innerHeight)*100>40 ||
			(((state.moveProperties.jetY)-(action.pageY-state.moveProperties.pointerY))/window.innerHeight)*100<0
		){
			return state;
		}else {
			return {
				 ...state,
						jetPosition:{
							...state.jetPosition,
							bottomDistance:(((state.moveProperties.jetY)-(action.pageY-state.moveProperties.pointerY))/window.innerHeight)*100+'vh',
							
						}
						
			 } 
			 }
	 
	 
	 
	 
	 }else if (action.type=='mouseXMover' && state.moveProperties.touched=='yes' ) {
		 
		if ((((state.moveProperties.jetX)+(action.pageX-state.moveProperties.pointerX))/window.innerHeight)*100>((window.innerWidth/window.innerHeight*100)-10) ||
			(((state.moveProperties.jetX)+(action.pageX-state.moveProperties.pointerX))/window.innerHeight)*100<0
		){
			return state;
		}else {
			return {
				 ...state,
						jetPosition:{
							...state.jetPosition,
							leftDistance:(((state.moveProperties.jetX)+(action.pageX-state.moveProperties.pointerX))/window.innerHeight)*100+'vh',
							
						}
						
			 } 
			 }
	 }else if (action.type=='mouseRelease') {
		 
		 return {
			 ...state,
				moveProperties: {
					...state.moveProperties,
						touched:'no'
				}
		 }
	 }else if (action.type=='destroyMonster') {
		 return {
		 ...state,
			monsterCollection: {
				...state.monsterCollection,
					[action.payload]: {
						...state.monsterCollection[action.payload],
						display:'none'	
					}
			}
		 
	 }
	 }
		else {
		 return state;
	 }
	
}

const store=createStore(myReducer);

ReactDOM.render(<Provider store={store}><App /></Provider>, document.getElementById('root'));

